import React from 'react';
import { Select } from 'antd';
import { selectObject } from "./select-config";

const hospitalList = JSON.parse(localStorage.getItem('hospitalList')) || selectObject
console.log(hospitalList)

if (!JSON.parse(localStorage.getItem('hospitalList'))) {
  localStorage.setItem('hospitalList', JSON.stringify(selectObject));
  console.log('abc')
}


const { Option } = Select;

class SelectComponent extends React.Component {

  handleChange = (value) => {

    this.props.changeHospital(`${value}`)
  }

  showOption = () => {
    const result = hospitalList.map((item, index) => {
      return <Option value={item.value} key={index}>{item.name}</Option>
    })

    return result
  }

  render() {
    return (
      <Select defaultValue={this.props.default} style={{ width: 120 }} onChange={this.handleChange}>
        {this.showOption()}
      </Select>

    )
  }
}

export default SelectComponent